/* eslint-disable no-promise-executor-return */
import { IssueDetail } from '../../foundation/utils.js';
import { getTypeChild, isTypeMissing } from './foundation.js';

export async function dOValidator(element: Element): Promise<IssueDetail[]> {
  if (isTypeMissing(element))
    return [
      {
        title: `validator.templates.missingAttribute, type, ${element.tagName}`,
        message: `${element.tagName}`,
      },
    ];

  const child = getTypeChild(element);
  if (child === null)
    return [
      {
        title: `validator.templates.missingReference, DO, ${element.getAttribute(
          'name'
        )}`,
        message: `${element.tagName}`,
      },
    ];

  return [];
}
